* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }


.layout {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff; }

.app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e0e6ee;
  border-radius: 8px; }

.app-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 35px;
  height: 100px; }

.app-data {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-items: normal;
  margin: 16px; }

.panel {
  margin-bottom: 16px; }
  .panel .panel-item {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    min-width: 302px;
    height: 48px;
    background-color: #ffffff;
    box-shadow: 0px 2px 1px rgba(196, 196, 196, 0.2);
    border-radius: 4px; }
    .panel .panel-item h3 {
      font-style: normal;
      font-weight: bold;
      font-size: 16px;
      margin-left: 10px;
      color: #00687f; }
    .panel .panel-item button {
      all: unset;
      position: absolute;
      right: 10px;
      height: 18px; }
  .panel .panel-collapse {
    background-color: #ffffff;
    overflow: hidden;
    -webkit-transition: height 0.3s ease-out;
    transition: height 0.3s ease-out; }
    .panel .panel-collapse .panel-body {
      position: relative;
      background-color: #ffffff; }

table {
  color: #474747;
  font-size: 12px;
  padding: 0px;
  width: 100%;
  border-collapse: collapse; }

th {
  color: #474747;
  border-bottom: 1px solid #c4c4c4; }
  th:nth-of-type(1) {
    text-align: left; }

td,
th {
  padding: 10px;
  text-align: right; }

td:nth-of-type(1) {
  text-align: left;
  color: #00687f; }

.align-center {
  display: flex;
  flex-direction: column;
  height: 100px; }

@media only screen and (max-width: 760px), (max-device-width: 768px) and (max-device-width: 1024px) {
  table,
  thead,
  tbody,
  th,
  td,
  tr {
    margin: 0px;
    text-align: left;
    display: block; }
  table {
    padding: 0px; }
  thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px; }
  tr {
    background-color: #ffffff;
    box-shadow: rgba(196, 196, 196, 0.2); }
    tr:nth-of-type(even) {
      background-color: rgba(229, 229, 229, 0.5); }
  tr td {
    border: none;
    position: relative;
    padding-left: 50%; }
    tr td:nth-of-type(1) {
      color: #00687f; }
  td:before {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 45%;
    color: #474747;
    padding-right: 10px;
    white-space: nowrap; }
  td:nth-of-type(1):before {
    content: 'Planet Name'; }
  td:nth-of-type(2):before {
    content: 'Rotation peroid'; }
  td:nth-of-type(3):before {
    content: 'Orbital peroid'; }
  td:nth-of-type(4):before {
    content: 'Diameter'; }
  td:nth-of-type(5):before {
    content: 'Climate'; }
  td:nth-of-type(6):before {
    content: 'Surface water'; }
  td:nth-of-type(7):before {
    content: 'Population'; } }

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-items: flex-end;
  height: 70px;
  font-weight: 300;
  font-size: 12px;
  color: #999999; }
  .footer a {
    text-decoration: none;
    color: inherit; }

.app-user-movie {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-items: normal;
  margin: 16px; }

.panel2 .panel2-item {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  min-width: 302px;
  height: 48px;
  background-color: #ffffff;
  box-shadow: 0px 2px 1px rgba(196, 196, 196, 0.2);
  border-radius: 4px; }
  .panel2 .panel2-item h3 {
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    margin-left: 10px;
    color: #00687f; }
  .panel2 .panel2-item button {
    all: unset;
    position: absolute;
    right: 10px;
    height: 18px; }

.panel2 .panel2-collapse {
  background-color: #ffffff;
  overflow: hidden;
  -webkit-transition: height 0.3s ease-out;
  transition: height 0.3s ease-out; }
  .panel2 .panel2-collapse .panel2-body {
    background-color: #ffffff; }
    .panel2 .panel2-collapse .panel2-body .elements-container {
      justify-content: flex-start;
      padding: 10px; }
      .panel2 .panel2-collapse .panel2-body .elements-container .button-align {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center; }

.badges-wrapper {
  list-style-type: none; }

.badge {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 1px solid #999999;
  border-radius: 18px;
  max-width: 132px;
  height: 32px;
  margin-top: 15px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  color: #474747; }
  .badge span {
    height: 20px;
    line-height: 19px; }
  .badge .badge-button {
    all: unset;
    margin-left: 13px; }
    .badge .badge-button svg {
      pointer-events: none; }

.submit-button {
  all: unset;
  background: #1ba1be;
  border-radius: 4px;
  margin-top: 35px;
  margin-bottom: 2%;
  min-width: 282px;
  color: #ffffff;
  height: 33px;
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  letter-spacing: 0.05em; }

.submit-button:disabled {
  cursor: not-allowed;
  background-color: #e0e6ee;
  color: #ffffff; }

.input-planet-wrapper {
  margin-top: 13px; }

.input-wrapper {
  display: flex;
  flex-direction: column; }
  .input-wrapper label {
    color: #555555;
    font-size: 12px; }
  .input-wrapper .invalid {
    color: #ff1616; }
  .input-wrapper input {
    border: none;
    border-bottom: 1px solid #999999;
    font-size: 16px;
    padding-left: 3px;
    height: 32px;
    color: #555555; }
  .input-wrapper :focus {
    outline: none;
    background-color: #fdffe2; }

.option {
  margin: 11px 11px 11px 11px; }
  .option span {
    color: #555555; }

.data-list {
  z-index: 999;
  min-width: 283px;
  background-color: #ffffff;
  position: absolute;
  border: 1px solid #e5e5e5;
  box-shadow: 0px 4px 4px rgba(196, 196, 196, 0.5); }
  .data-list ul {
    list-style-type: none; }

.prompt {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  z-index: 700;
  display: none;
  background-color: #ffffff;
  border: 1px solid #ff1616;
  height: 32px;
  box-shadow: 0px 4px 4px rgba(196, 196, 196, 0.5); }
  .prompt span {
    font-size: 12px;
    margin-left: 11px;
    color: #ff1616; }

.invalid {
  display: block; }

